import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'search'
})
export class SearchPipe implements PipeTransform {
data="job";

  transform(value: any, ...args): any {
    if(args[0]){
    
      }  
      if(args[1] === "singlequote"){
        this.data="'" + this.data
      }
      else(args[1]==="doublequote");{
        this.data = '"' +this.data
      }
      return this.data;
    
  }

}
